
@media screen and (max-width:950px){

    .center{
        display: flex;
        align-items: center;
    }

    .Block_2,.Block_3,.Block_4,.Mobile_Block_3,.Block_5{
        width: 100%;
        height: auto;
        background-color: #1a1b1f;
        position: relative;
        
        /* display: none; */
    }

    .Block_5{
        display: unset;
    }
    
    .layer_a{
        width:100%;
        height: auto;
        /* display: flex;
        align-items: center; */
        background-color: #1a1b1f;
        /* background-color: rgba(114, 114, 114,1); */
    }

    .inseide_console_layer_a{
        width: 100%;
        height: 500px;
        position: relative;
        margin: auto;

        /* background-color: aqua; */
                
        background-color: #1a1b1f;
        
    }

    .inside_terminal_layer_a{
        width: 80%;
        height: 80%;
        
        background-color: rgb(0, 0, 0);

        border-radius: 10px 10px;
        margin: auto;
        box-shadow: 10px 10px 4px -10px rgb(178, 178, 178) inset;
        position: relative;

        overflow:hidden;

        color: rgb(129, 211, 57);

    }

    .inside_teminal_image_layer_a{
        width: 250px;
        height: 250px;
        margin: auto;
        margin-top: 2%;
        margin-left: 30%;
        position: absolute;
        
        border-radius: 10px 10px;

        background-color: rgba(50, 50, 50, 0.317);

        border-radius: 10px 10px;
        box-shadow: 10px 10px 4px -10px rgb(99, 99, 99) inset;

        display:none;

    }

    .font_size_layer_a{
        font-size: 100%;
    }


    .inseide_text_layer_a{
        width: 100%;
        height: auto;
        position: relative;

        background-color: #1a1b1f;

        
        /* background-color: rgb(147, 147, 147); */
    }

    .inseide_text_layer_a_inside{
        width: 100%;
        height: auto;

        position: relative;
        margin: auto;
        /* border: solid 4px rgb(30, 163, 38); */
        /* border-radius: 10px 10px; */
        /* background-color: rgb(30, 30, 30); */

        border-bottom:solid;
        border-color:rgb(30, 163, 38);
        border-width:3px;/*5ピクセルの太さにする*/
        
    }

    .inseide_text_title_layer_a{
        width: 80%;
        height: auto;
        color: white;
        margin: auto;

        /* background-color: #1a1b1f; */
        position: relative;
    }
    .inseide_text_title_layer_a_inside{
        width: 100%;
        height: 100%;
        margin: auto;
        font-size: 20px;
        text-align: center;

        /* background-color: aliceblue; */
    }
    .inseide_text_text_layer_a_inside{
        width: 80%;
        height: auto;
        
        position: relative;
        top: 5%;
        /* background-color: white; */
        margin: auto;
        font-size: 20px;
        color: white;
        
    }

}

