.Mother{
    width: auto;
    height: auto;

    background-color: white;
}

.toppage{
    width: 100%;
    height: 100vh;
    background: #1a1b1f;


}

#app {
    width: 400px;
    margin: 40px auto;
    padding: 28px;
    line-height: 1.5;
    border: 1px solid #ffffff;
    border-radius: 5px;
    background-color: transparent;
    box-shadow: 2px 2px 5px 0px rgba(200, 200, 200, 1);
    font-family: "Times New Roman";
}

#typewriter,#typewriter2,#typewriter3,#typewriter4,#typewriter5{
    color: rgb(129, 211, 57);
    font-size: 1.6vh;
    
}

#typewriter5::after ,#typewriter_layer_b_4::after,#typewriter_layer_c_2::after{
    content: "|";
    animation-name: blink;
    animation-duration: 1s;
    animation-iteration-count: infinite;
}

@keyframes blink {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.Event{
    width: 100%;
    height: 100vh;
    background-color: white;
}
.img,.text_message{
    width: 50%;
    height: 100vh;
    float: left;
    background-color: #1a1b1f;
    
    display: flex;
    align-items: center;

    overflow: hidden;
    
    

}
.img{
    background-color: #070707;
}


.img_frame{
    width: 100%;
    height: 100%;
}
.IMG{
