.UnderLine{
    border-bottom:solid;
    border-color:rgb(30, 163, 38);
    border-width:2px ;/*5ピクセルの太さにする*/
}

.CenterBlock{
    width: auto;
    height: auto;

}

.Block{
    width: 100%;
    height: auto;
    /* background-color: #b8b8b8; */
    position: relative;

    z-index: 999;


    /* background-color: white; */

    /* background-image:url(../IMAGE/Mirai.jpg);
    background-size:contain;
    background-position:bottom;
    background-repeat:no-repeat; */
}

.layer_b{
    width:1900px;
    height: 1000px;
    margin: auto;
    margin-top: 5%;
    
    /* display: flex;
    align-items: center; */
    position: relative;
/* background-color: rgba(114, 114, 114,1); */
    
}


.outside_layer_b{
    width: 100%;
    height: 650px;
    position: absolute;
    /* background-color: #8e97a0; */
    margin-top: 10%;
    border-bottom:solid;
    border-color:rgb(30, 163, 38);
    border-width:3px;/*5ピクセルの太さにする*/


}

.inside_layer_b,.inside_layer_c,.inside_layer_d{
    width: 500px;
    height: 600px;
    background-color: rgb(30, 30, 30);
    
    border: solid 3px rgb(30, 163, 38); 
    border-top-right-radius: 30px 30px;
    border-top-left-radius: 30px 30px;

    display: flex;
    align-items: center;

    float: left;
    margin-left: 6%;

    box-shadow: 10px 10px 10px 2px rgb(30, 163, 38, 0.279); 
}

.inside_layer_btext_frame{
    width: 90%;
    height: 95%;
    margin: auto;
}
.inside_layer_btext_frame_top{
    width: auto;
    height: 10%;
    
    margin: auto;
    text-align: center;
    color: white;

    /* background-color: #1a1b1f; */

    font-size: 13px;

    border-bottom:solid;
    border-color:rgb(30, 163, 38);
    border-width:3px;/*5ピクセルの太さにする*/
}

.inside_layer_btext_frame_bottom{
    width: auto;
    height: 85%;
    
    margin: auto;
    color: white;

    font-size: 14px;

    /* background-color: white; */
    text-align: center;
    position: relative;
}
.inside_layer_btext_frame_image{
    width: 80%;
    height: 40%;

    margin: auto;
    background-color: rgba(229, 229, 229, 0.07);
}






.inside_title_layer_b{
    width: 100%;
    height: 100px;
    
    
    background-color: rgba(244, 244, 244, 0);
    position: absolute;    
}
.inside_title_layer_b_inside{
    width: 600px;
    height: auto;
    
    margin: auto;
    text-align: center;

    font-size: 50px;
    color: rgb(241, 241, 241);

    text-shadow:
        0 2px 0 rgb(189, 189, 189),
        0 3px 0 rgb(91, 132, 65),
        0 4px 5px rgb(30, 163, 38);
    /* background-color: white; */
}

.inside_price_layer_b{
    width: 100%;
    height: auto;
    margin-top: 44%;
    /* background-color: white; */
    position: absolute;
}
.inside_price_layer_b_inside{
    width: 600px;
    height: auto;
    
    
    margin: auto;
    text-align: center;
    
    border-radius: 10px 10px;
    margin-top: -3%;
    /* border: solid 2px rgb(30, 163, 38); */
    
    font-size: 60px;

    color          : #1a1b1f;            
    font-size      : 60px;               
    letter-spacing : 2px;             
    text-shadow    : 
        2px  2px 1px rgb(30, 163, 38),
        -2px  2px 1px rgb(30, 163, 38),
        2px -2px 1px rgb(30, 163, 38),
        -2px -2px 1px rgb(30, 163, 38),
        2px  8px 1px rgb(30, 163, 38),
        0px  2px 1px #003366,
        -2px  0px 1px #003366,
        0px -2px 1px #003366;        

    /* box-shadow: 2px 3px 10px 10px rgb(30, 163, 38, 0.279);  */

}

.outside_pay_layer_b{
    width: 1000px;
    height: 900px;
    
    border-top-right-radius: 10px 10px;
    border-top-left-radius: 10px 10px;

    position: relative;
    margin: auto;
}
.inside_pay_layer_b{
    
    width: 800px;
    height: 80%;
    
    /* background-color: gray; */
    margin: auto;
    position: relative;
}
.inside_pay_layer_b_title{
    width: 100%;
    height: 15%;
    position: relative;
    
    display: flex;
    align-items: center;
    /* background-color: #1a1b1f; */
    
}
.inside_pay_layer_b_title_inside{
    width: 200px;
    height: auto;
    
    font-size: 12px;
    margin: auto;
    color: white;
    text-align: center;

    position: relative;
}

.inside_input_layer_b{
    width: 100%;
    height: 80%;
    position: absolute;
    text-align: center;

    /* background-color: rgba(16, 16, 16, 0.815); */
    
    background-color: rgb(0, 0, 0);

    border-radius: 10px 10px;
    box-shadow: 10px 10px 4px -10px rgb(99, 99, 99) inset;

    color: rgb(129, 211, 57);
}

.inside_username_layer_b,.inside_mail_layer_b,.inside_message_layer_b{
    width: 50%;
    height: 7%;
    margin: auto;

    margin-top: 5%;
    
    background-color: white;
}
.inside_message_layer_b{
    height: 50%;
}

.inside_input_layer_b_input{
    width: 100%;
    height: 100%;
    
}

.inside_paymeint_layer_b{
    width: 60%;
    height: 30%;
    margin: auto;
    border-radius: 10px 10px;
    margin-top: 10%;
    border: solid 3px rgb(30, 163, 38);
}
.inside_paymeint_layer_b_btn{
    width: 60%;
    height: 30%;
    background-color: #285e33;

    margin: auto;
}
