@media screen and (max-width:950px){
    
    

    .toppage{
        width: 100%;
        height: 100vh;
        background: #1a1b1f;
    }

    #app {
        width: 400px;
        margin: 40px auto;
        padding: 28px;
        line-height: 1.5;
        border: 1px solid #ffffff;
        border-radius: 5px;
        background-color: transparent;
        box-shadow: 2px 2px 5px 0px rgba(200, 200, 200, 1);
        font-family: "Times New Roman";
    }

    #typewriter,#typewriter2,#typewriter3,#typewriter4,#typewriter5{
        color: rgb(129, 211, 57);
        font-size: 1.6vh;
                        
    }

    #typewriter5::after {
        content: "|";
        animation-name: blink;
        animation-duration: 1s;
        animation-iteration-count: infinite;
    }

    @keyframes blink {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }

    .Event{
        width: 100%;
        height: 100vh;
        background-color: white;
    }
    
    .text_message{
        width: 100%;
        height: 100vh;
        float: left;
        background-color: #1a1b1f;
        
        background:linear-gradient(#23202044, #000000);

        
        display: flex;
        align-items: center;

        overflow: hidden;
        position: relative;
        
        z-index: 2;

        


    }
    .img{
        background-color: #070707;
        /* display: none; */
        /* position: absolute; */
        width: 100%;
        height: 100%;
        z-index: 1;
        
        overflow: hidden;
        position: fixed;
    }
    #img_center_line{
        width: 20%;
        height: 10%;
        margin: auto;
        /* background-color: white; */
        
        margin-top: -31%;
        position: relative;
        
        
    }

    .img_frame{
        width: 100%;
        height: 100%;
    }
    .IMG{
        width: 100%;
        height: auto;
        background-color: rgb(226, 226, 226);
        
        margin-top: 15%;
        /* box-shadow: 1px 1px 0px 10px rgba(20, 20, 20, 0.37);  */
    }


    .color_g{
        color: rgb(129, 211, 57);
    }
    .color_w{
        color: rgb(222, 222, 222);
    }

    .fontSize_50{
        font-size: 30px;
        /* box-shadow: 1px 1px 0px 1px rgb(30, 163, 38);  */
    }

    .notSelect{
        user-select: none;
        
    }


    .message_fram{
        width: 100%;
        height: auto;
        
    }

    .terminal_message_frame{
        width: 100;
        height: 35vh;
        border-radius: 10px 10px;
        background-color: rgb(0, 0, 0);
        margin: auto;
        margin-top: 20%;
        box-shadow: 10px 10px 4px -10px rgb(178, 178, 178) inset;

        overflow:hidden;

        /* box-shadow: 0px 10px 10px -5px rgba(14, 14, 14, 0.712); */

    }

    .guide_message_frame{
        width: 90%;
        height: 250px;
        margin: auto;
        margin-top: 5%;
        text-align: center;
        
        
    }

}